.ToDoListItem {
    padding : 1rem;
    display : flex;
    align-items: center;
    &:nth-child(even) {
        background : #f8f9fa;
    }
    .checkBox {
        cursor : pointer;
        flex : 1;
        display: flex;
        align-items: center;
        svg {
            font-size : 1.5rem;
        }
        .text {
            margin-left : 0.5rem;
            flex:1;
        }
        /*checked 됐을 때 보여줄 스타일*/
        &.checked {
            svg {
                color : #22b8cf;
            }
            .text {
                color : #adb5bd;
                text-decoration : line-through;
            }
        }
    }
    .remove {
        display : flex;
        align-items: center;
        color : #ff6b6b;
        cursor : pointer;
        &:hover {
            color : #ff8787;
        }
    }
    & + & {
        border-top : 1px solid #dee2e6;
    }
}
.ToDoListItem-virtualized {
    & + & {
        border-top : 1px solid #dee2e6;
    }

    &:nth-child(even){
        background:#f8f9fa;
    }
}